.h2{
  font-size: calc(10px + 2vmin);
}
.h3{
  font-size: calc(10px + 1vmin);
}
.App {
  text-align: center;
}

.my-pic {
  height: 20vmin;
  border-radius: 50%;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  width: 1000px;
  margin: 10px auto;
  display: flex;
  flex-direction: row;
  align-items: top;
  justify-content: left;
  color: white;
  box-shadow: 1px 1px 3px #999999;
}

.App-link {
  color: #61dafb;
}

.App-left{
  width: 250px;
  padding: 10px;
}

.App-right{
  background-color: #353c48;
  width: 750px;
  padding: 10px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
